import CustomButton from "./CustomButton"
import CustomInput from "./CustomInput"
import GaugeMeter from "./GaugeMeter"
import useNow from '../hooks/useNow'
import Overlay, { CaptureOverlay } from "./Overlay"
import { DayNightCircle } from "./RotatingSegments"
import { useRef } from "react"
import "./DayMeter.scss"

const NIGHT = 22
const MORNING = 4
const MIDDAY = 10
const EVENING = 17

export default function ({
  value,
  subValue,
  points,
  isRunning,
  onChange,
  onInc,
  onPlay,
  onPause,
}) {
  const { now } = useNow(1000 * 60)
  const date = new Date(now)
  const startMinute = date.getHours() * 60 + date.getMinutes()
  const ref = useRef()
  const a = - startMinute / 60 * 180 / 24

  const fireColor = "#e9ca99"
  const ticks = []
  const I = []
  for (let i = 0; i < 24 * 2; i += 6) {
    I.push(i)
  }

  const nextHour = (date.getHours() + 1) % 24
  if (I.indexOf(nextHour) === -1) {
    I.push(nextHour)
  }

  const nextNextHour = (date.getHours() + 2) % 24
  if (I.indexOf(nextNextHour) === -1) {
    I.push(nextNextHour)
  }

  for (const i of I) {
    const h = i % 24
    let color;
    color = "rgba(0,0,0,0.75)"
    if (NIGHT < h || h < MORNING) {
      color = "rgba(255,255,255,0.75)"
    }
    ticks.push({
      value: i * 60 - startMinute,
      tickType: "large",
      label: h + ":00",
      color,
    });
  }

  const clientWidth = window.innerWidth
  const clientHeight = clientWidth / 2

  return (
    <div>

      <div style={{
        width: clientWidth + "px",
        height: clientHeight + "px",
      }}>
        <div style={{
          position: "absolute",
          overflow: "hidden",
          top: "5px",
          height: clientHeight - 10 + "px",
          width: clientWidth + "px",
        }}>
          {/* {console.log("clientHeight",  ref.current.clientHeight)}
          {console.log("clientWidth",  ref.current.clientWidth)} */}
          <DayNightCircle
            style={{
              position: "relative",
              left: "-50%",
              // zIndex: 1000,
            }}
            height={clientHeight * 2 - 30}
            width={clientWidth * 2}
            angles={[
              (a - 180 + 180 / 24 * (NIGHT - 24)) % 360,
              (a - 180 + 180 / 24 * MORNING) % 360,
              (a - 180 + 180 / 24 * MIDDAY) % 360,
              (a - 180 + 180 / 24 * EVENING) % 360,
              (a - 180 + 180 / 24 * NIGHT) % 360,
              (a - 180 + 180 / 24 * (24 + MORNING)) % 360,
              (a - 180 + 180 / 24 * (24 + MIDDAY)) % 360,
              (a - 180 + 180 / 24 * (24 + EVENING)) % 360,
            ]}
          />
        </div>
        <div
          ref={ref}
          style={{
            position: "relative",
            width: "100%",
            height: "100%",
          }}
        >
          <div
            style={{
              position: "absolute",
              left: "calc(50% - 30px)",
              top: "calc(100% - 42px)",
            }}
          >
            <Overlay
              minXOffset={0}
              minYOffset={0}
              base={
                <div>
                  <CustomInput
                    className="gauge-points-handler"
                    style={{
                      // border: "1px solid black",
                      // background: "rgba(255,255,255,0.5)",
                      fontSize: "18px",
                      letterSpacing: "2px",
                      width: "60px",
                      height: "40px",
                      textAlign: "center",
                      fontFamily: "serif",
                    }}
                    value={value}
                    onChange={e => onChange(parseInt(e.target.value) || 0)}
                  />
                  {/* <div className="gauge-points-sub-value" style={{}}>
                  {subValue}
                </div> */}
                </div>
              }
              overlay={
                <div className="gauge-overlay">
                  <CaptureOverlay>
                    {isRunning && (
                      <CustomButton
                        onClick={() => onPause()}
                        className="gauge-action gauge-action-play"
                      >
                        PAUSE
                      </CustomButton>
                    )}
                    {!isRunning && (
                      <CustomButton
                        onClick={() => onPlay()}
                        className="gauge-action gauge-action-play"
                      >
                        PLAY
                      </CustomButton>
                    )}
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(-30)}
                      className="gauge-action"
                    >
                      -30
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(-15)}
                      className="gauge-action"
                    >
                      -15
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(-10)}
                      className="gauge-action"
                    >
                      -10
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(-5)}
                      className="gauge-action"
                    >
                      -5
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(-1)}
                      className="gauge-action"
                    >
                      -1
                    </CustomButton>
                  </CaptureOverlay>
                  <div className="gauge-input-sep" />
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(+1)}
                      className="gauge-action"
                    >
                      +1
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(+5)}
                      className="gauge-action"
                    >
                      +5
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(+10)}
                      className="gauge-action"
                    >
                      +10
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(+15)}
                      className="gauge-action"
                    >
                      +15
                    </CustomButton>
                  </CaptureOverlay>
                  <CaptureOverlay>
                    <CustomButton
                      onClick={() => onInc(+30)}
                      className="gauge-action"
                    >
                      +30
                    </CustomButton>
                  </CaptureOverlay>
                </div>
              }
            />
          </div>

          <GaugeMeter
            animate={isRunning}
            style={{
              width: clientWidth + "px",
              height: clientHeight + "px",
            }}
            min={0}
            max={24 * 60}
            value={Math.max(0, Math.min(24 * 60, value))}
            ticks={ticks}
            fireColor={fireColor}
          />
        </div>
      </div>
      <div style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "10px",
      }}>
        <div>
          {points}
        </div>
      </div>
    </div>
  )
}
